import React from 'react';
import Data from './www.freelancer.com.json';
import { Link } from 'react-router-dom';

export default function GallaryDetails({match}) {
    let { params: { tagId: activeTag } } = match;
    console.log(activeTag) 
    let details = [];
    let diffTags = ["PHP","JavaScript","CakePHP","MySQL","React.js","React.js Framework","Redux.js","Website Design","HTML","Graphic Design","Mobile App Development","React Native","Bootstrap","AngularJS","Software Architecture","Data Processing","Data Entry","Excel","Web Scraping","Web Search","CSS","HTML5","WordPress","Google Chrome","Extensions & Additions","Chrome OS","Firefox","Node.js","Express JS","PostgreSQL","MongoDB","Animation","AJAX","iPhone","Android","PhoneGap","Python","Linux","NoSQL Couch & Mongo","SVG","backbone.js","XML","Ruby on Rails","SQL"];
    if(activeTag === 'all') {
        details = Data;
    } else {
        activeTag = activeTag.split("-").join(" ").toUpperCase();
        details = Data.filter(item => {
            if(item.tags) {
                let tgs = item.tags.map(t => t.toLowerCase())
                return tgs.indexOf(activeTag.toLowerCase()) > -1
            } else return false;
        });
    }
    console.log(details, activeTag)
    
    return (
        <div className="main-content">
            <div className="fluid-container">
                <div className="content-wrapper">
                    {/*<!-- PROJECTS -->*/}
                    <div className="page-section" id="projectsdetails">
                        <div className="row">
                            <div className="col-md-12">
                                <h4 className="widget-title">Skill Tag: {activeTag}</h4>
                                {
                                    diffTags.map(tag => {
                                        return (<span key={tag}><Link to={"/gallary/"+tag}>{tag}</Link>&nbsp;&nbsp;&nbsp;</span>)
                                    })
                                }
                                {
                                    details.map((item, index) => {
                                        //let time = new Date(item.time);
                                        let rating = {}
                                        if(item.rating && item.rating.category_ratings) rating = item.rating.category_ratings
                                        return (
                                        <div className="projectCart" key={item.desc}>
                                            <h3 style={{fontSize: 12}}>{index+1}</h3>
                                            <h3>Project Title: {item.ptitle}</h3>
                                            <h4><span>Project Description: </span>{item.pdesc}</h4>
                                            
                                            <p className="reviecomment" ><span>Review Comment: </span>{item.desc}<br/>
                                                <span>HIRE AGAIN: {rating.hire_again}</span>
                                                &nbsp;| <span>Communication: {rating.communication}</span>
                                                &nbsp;| <span>Qaulity Of Work: {rating.quality}</span>
                                                &nbsp;| <span>Professionalism: {rating.professionalism}</span>
                                                &nbsp;| <span>Expertise: {rating.expertise}</span>
                                            </p>
                                            <p>
                                                Project Skill Tags: {item.tags && item.tags.filter(t => t !== 'Hire me').join(", ")}
                                            </p>
                                            <p className="linn" >
                                                <a target="_blank" rel="noopener noreferrer" href={"https://www.freelancer.com/"+item.url}>Project Link</a>
                                            </p>
                                        </div>
                                        )
                                    })    
                                }
                            </div>
                        </div>
                        <div className="row projects-holder">
                            
                        </div> {/*<!-- .projects-holder -->*/}
                    </div>
                </div>
            </div>
        </div>
    );
}