import React, { useState } from 'react';

export default function ContactMe() {
    const [mail, setMail] = useState({
        firstname: "",
        subject: "",
        message: ""
    });

    const mailTo = (e) => {
        e.preventDefault();
        let body = encodeURIComponent(`
        Hi Uttam,
        I am ${mail.firstname}.
        ${mail.message}
        -----------------------------------------
        Date: ${(new Date())}`);
        window.location.href = `mailto:freelancerseo001@gmail.com?subject=${mail.subject}&body=${body}`;
    };

    return (
        <div className="main-content">
            <div className="fluid-container">
                <div className="content-wrapper">
                    {/*<!-- CONTACT -->*/}
                    <div className="page-section" id="contact">
                        <div className="row">
                            <div className="col-md-12">
                                <h4 className="widget-title">PLACE TO TALK WITH ME</h4>
                                <p>Please let me know how can I help you?</p>
                            </div>
                        </div>
                        <div className="row">
                            <form action="#" method="post" className="contact-form" onSubmit={mailTo}>
                                <fieldset className="col-md-6 col-sm-6">
                                    <input type="text" onChange={(e) => setMail({...mail, firstname: e.currentTarget.value})} id="your-name" placeholder="Your Name..." />
                                </fieldset>
                                <fieldset className="col-md-6 col-sm-12">
                                    <input type="text" onChange={(e) => setMail({...mail, subject: e.currentTarget.value})} id="your-subject" placeholder="Subject..." />
                                </fieldset>
                                <fieldset className="col-md-12 col-sm-12">
                                    <textarea name="message" onChange={(e) => setMail({...mail, message: e.currentTarget.value})} id="message" cols="30" rows="6" placeholder="Leave your message..."></textarea>
                                </fieldset>
                                <fieldset className="col-md-12 col-sm-12">
                                    <input type="submit" className="button big default" value="Send Message" />
                                </fieldset>
                            </form>
                        </div> {/*<!-- .contact-form -->*/}
                    </div>
                    <hr />


                </div>

            </div>
        </div>
    );
}