import React, {useState} from 'react';
import { Link, useHistory, useLocation } from "react-router-dom";


function Header() {
    const [showMenu, setShowMenu] = useState(false);
    let loc = useLocation();
    const history = useHistory();
    loc = loc.search.replace("?", "").split("=");
    if(loc[0] && loc[0] === "page") {
        history.push("/"+loc[1]);
    }
  return (
    <>
            <div className="responsive-header visible-xs visible-sm">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <div className="top-section">
                            <div className="profile-image">
                                <img src={require('../img/avatar.png')} alt="Uttam" />
                            </div>
                            <div className="profile-content">
                                <h3 className="profile-title">Uttam</h3>
                                <p className="profile-description">Software Enggineer</p>
                            </div>
                        </div>
                    </div>
                </div>
                <button onClick={() => setShowMenu(!showMenu)} className="toggle-menu"><i className="fa fa-bars"></i></button>
                <div className="main-navigation responsive-menu" style={{display: (showMenu ? 'block' : 'none')}}>
                    <ul className="navigation">
                    <li><Link to="/"><i className="fa fa-globe"></i>Welcome</Link></li>
                    <li><Link to="/about"><i className="fa fa-pencil"></i>About Me</Link></li>
                    <li><Link to="/gallary"><i className="fa fa-paperclip"></i>My Gallery</Link></li>
                    <li><Link to="/contact"><i className="fa fa-link"></i>Contact Me</Link></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.freelancer.in/hireme/CakephpJobs1"><i className="fa fa-link"></i>Hire Me</a></li>
                    
                    </ul>
                </div>
            </div>
        </div>

        {/* SIDEBAR */}
        <div className="sidebar-menu hidden-xs hidden-sm">
            <div className="top-section">
                <div className="profile-image">
                    <img src={require('../img/avatar.png')} alt="Uttam" />
                </div>
                <h3 className="profile-title">Uttam</h3>
                <p className="profile-description">Software Engineer</p>
            </div> {/* top-section */}
            <div className="main-navigation">
                <ul className="navigation">
                    <li><Link to="/"><i className="fa fa-globe"></i>Welcome</Link></li>
                    <li><Link to="/about"><i className="fa fa-pencil"></i>About Me</Link></li>
                    <li><Link to="/gallary"><i className="fa fa-paperclip"></i>My Gallery</Link></li>
                    <li><Link to="/contact"><i className="fa fa-link"></i>Contact Me</Link></li>
                    <li><a target="_blank" rel="noopener noreferrer" href="https://www.freelancer.in/hireme/CakephpJobs1"><i className="fa fa-link"></i>Hire Me</a></li>
                    
                </ul>
            </div> {/* .main-navigation */}
            {/*<div className="social-icons">
                <ul>
                    <li><a href={() => {}}><i className="fa fa-facebook"></i></a></li>
                    <li><a href={() => {}}><i className="fa fa-twitter"></i></a></li>
                    <li><a href={() => {}}><i className="fa fa-linkedin"></i></a></li>
                    <li><a href={() => {}}><i className="fa fa-google-plus"></i></a></li>
                    <li><a href={() => {}}><i className="fa fa-youtube"></i></a></li>
                    <li><a href={() => {}}><i className="fa fa-rss"></i></a></li>
                </ul>
            </div> .social-icons */}
        </div> {/* .sidebar-menu */}
    </>
  );
}

export default Header;
