import React from 'react';

import { Link } from "react-router-dom";
import Data from './data';

function SkillTag({tags}) {
    return (
        <>
            {tags.map(tag => {
                return (
                <button key={tag} type="button" className="btn btn-info tagbtn" >{tag}</button>)
            })}
        </>
    );
}

function MainTagCard({details}) {
    let allTags = details.tags.split(",");
    return (
        <div className="col-md-4 col-sm-6">
            <div className="project-item">
                <img src={require(`../img/${details.mainImg}`)} alt={details.name} />
                
                <div className="tags">
                    <SkillTag key={details.name} tags={allTags} />
                    <h5><Link to={"/gallary/"+details.details} > View Completed Project</Link></h5>
                    <p>{details.desc}</p>
                </div>
            </div>
        </div>
    );
}

export default function Gallary() {
    return (
        <>
        <div className="main-content">
            <div className="fluid-container">
                <div className="content-wrapper">
                    {/*<!-- PROJECTS -->*/}
                    <div className="page-section" id="projects">
                        <div className="row">
                            <div className="col-md-12">
                                <h4 className="widget-title">PHOTOS OF WHAT I DO</h4>
                                <p></p>
                            </div>
                        </div>
                        <div className="row projects-holder">
                            {Data.map((data, index) => {
                                return (<MainTagCard key={index} details={data} />)
                            })}
                        </div> {/*<!-- .projects-holder -->*/}
                        <h1 style={{fontSize: 20}}>
                                <Link to="/gallary/all">Check All By Tag</Link>
                            </h1>
                    </div>
                </div>
            </div>            
        </div>
        
        </>
    );
}